.d1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem;
  background: #f9fafb;
  cursor: pointer;
}

.d1:hover {
  background-color: #e5fcf6;
}

.expanded {
  color: #808080;
  width: auto;
  font-size: 10pt;
}

.icon {
  font-size: 10pt;
  color: #cccccc;
  display: flex;
}

._accitem,
._accitem,
._accitem,
._accitem,
._accitem,
._accitem,
._accitem,
._accitem,
._accitem,
._accitem,
._accitem,
._accitem {
  border-bottom: solid 1px #cccccc;
  border-top: solid 1px #cccccc;
}

._d1,
.TMS_d1,
.TAAS_d1 {
  border-bottom: solid 1px #cccccc;
}

._exp,
.TMS_exp,
.TAAS_exp {
  border-left: solid 5px #cccccc;
  border-bottom: solid 1px #cccccc;
}
